.navigation{display:flex;align-items:center}.navigationList{display:flex;justify-content:space-between;align-items:center;gap:calc(var(--space_base)*8)}.navigationList__item{width:50px;height:50px;display:flex;align-items:center;justify-content:center;border-radius:25px;box-shadow:var(--shadow_global-nav);transition:all .3s;position:relative}.navigationList__item:hover{background-color:var(--color_f-bg-reverse-default);color:var(--color_f-text-reverse-default);box-shadow:var(--shadow_global-nav-reverse);justify-content:center;transition:all .3s}